/* General Styles */

:root{
  --background-color:rgb(43, 43, 43);
  --background-color-1:rgb(52, 52, 52);
  --background-color-1-hover:rgb(77, 77, 77);
  --font-color:#d0d0d0;
  --button-color:rgb(87, 87, 87);
  --button-color-hover:rgb(117, 117, 117);
}
body {
  font-family: 'Arial', sans-serif;
  background-color:var(--background-color);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
}

body::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

body::-webkit-scrollbar-track {
  background: var(--background-color); /* Light grey background */
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb {
  background: var(--background-color-1-hover); /* Blue scrollbar */
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background:var(--button-color-hover); /* Darker blue on hover */
}

.container {
  /* width: 80%; */
  max-width: 1300px;
  background: var(--background-color);
  /* box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); */
  border-radius: 10px;
  overflow: hidden;
  padding: 20px;
  text-align: center;
  justify-content: center;
}

h1 {
  color: var(--font-color);
  
}

/* Drag & Drop Area */
.drop-area {
  border: 2px dashed var(--font-color);
  width: 80%;
  padding: 10px;
  margin: 20px auto;
  cursor: pointer;
  border-radius: 10px;
  background-color: var(--background-color-1);
  transition: background-color 0.3s ease-in-out;
  
}

.drop-area:hover {
  background-color: var(--background-color-1-hover);
}

.drop-area p {
  color: var(--font-color);
  font-weight: bold;
}

/* Layout */
.content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}
.left-half{
  flex: 1;
  max-width: 800px;
  padding: 20px;
  background: var(--background-color-1);
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  color: var(--font-color);
}
.right-half {
  flex: 1;
  max-width: 400px;
  padding: 20px;
  background: var(--background-color-1);
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  color: var(--font-color);
}

/* Preview Section */
.preview-section img {
  max-width: 100%;
  border-radius: 5px;
  margin-top: 10px;
}

.nav-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.nav-buttons button {
  background: var(--button-color);
  color: var(--font-color);
  border: none;
  padding: 8px 12px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.nav-buttons button:hover {
  background: var(--button-color-hover);
}

.extract-btn, .download-btn, .fetch-bank-details-btn {
  margin: 15px;
  background: var(--button-color);
  color: var(--font-color);
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.extract-btn:hover, .download-btn:hover , .fetch-bank-details-btn:hover{
  background: var(--button-color-hover);
}

/* Data Display */
.data-display {
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
  align-items: center;
}

.data-display h2 {
  color: #333;
}




.data-table tbody td ,.data-table thead th{
  padding: 10px 12px;

}
.data-table tr td div{
  word-wrap: break-word; 
  background-color: var(--background-color-1);
  max-width: 200px;
  min-width: 200px;
  /* width: 60%; */
  padding: 5px 10px;
  /* border: 1px solid #d1d5db; */
  border-radius: 8px;
  /* font-size: 14px; */
  color: var(--font-color);
  background-color: var(--background-color-1-hover);
  outline: none;
  transition: all 0.2s ease-in-out;

}
.data-table tr td input{
  font-family: 'Arial', sans-serif;
  font-size: 15px;
  background-color: var(--background-color-1);
  /* width: 75%; */
  max-width: 200px;
  min-width: 200px;
  padding: 6.5px 5px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  color: #374151;
  background-color: #f9fafb;
  outline: none;
  transition: all 0.2s ease-in-out;
}


.edit-btn{
  background: var(--button-color);
  color: var(--font-color);
  border: none;
  margin: 10px;
  padding: 8px 12px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s ease-in-out;
}

.edit-btn:hover{
  background: var(--button-color-hover);
}



.progress-bar-container {
  width: 100%;
  background-color: #f1f1f1;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 10px;
}

.progress-bar, .progress1-bar {
  margin: 10px;
  color: var(--font-color);
}

.table-container{
  overflow-x: auto;
  max-width: 100%;
  max-height: 500px;
}

.bank-details-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  -ms-overflow-style: none;  /* IE, Edge */
  scrollbar-width: none;
}

.bank-details-table::-webkit-scrollbar {
  display: none; /* Chrome, Safari */
}


.bank-details-table thead{
  position: sticky;
  top: -5px;
  z-index: 100;
}
.bank-details-table th, .bank-details-table td {
  /* border: 1px solid var(--font-color); */
  /* border-radius: 10px; */
  padding: 10px;
  text-align: left;
  color: var(--font-color);
}


.bank-details-table th {
  background-color: var(--background-color-1-hover);
  color: var(--font-color);
}

.bank-details-table tr:nth-child(even) {
  background-color:var(--background-color-1);
}

.table-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-container::-webkit-scrollbar-track {
  background: var(--background-color); /* Light grey background */
  border-radius: 10px;
}

.table-container::-webkit-scrollbar-thumb {
  background: var(--background-color-1-hover); /* Blue scrollbar */
  border-radius: 10px;
}

.table-container::-webkit-scrollbar-thumb:hover {
  background:var(--button-color-hover); /* Darker blue on hover */
}